import React from "react";
import "./styles/Project.css";
import { projects } from "./projects.js";
const Project = () => {
  return (
    <>
      <div className="projects-container">
        <h1 id="projects-title">
          <i className="uil uil-bolt"></i>My Works
        </h1>
        <div
          style={{
            display: "flex",
            flexWrap: "wrap",
            justifyContent: "center",
            alignItems: "center",
          }}
        >
          {projects.map((project) => {
            const { key, img, title, description, stars, github_url } = project;
            return (
              <article className="project-wrapper" key={key}>
                <div className="project">
                  <div
                    style={{ backgroundImage: `url(${img})` }}
                    className="project-header-img"
                  >
                    <div className="project-title">{title}</div>
                  </div>
                  <p>{description}</p>
                  <div className="star-wrapper">
                    <i className="uis uis-star"></i>
                    <p>{stars}</p>
                  </div>
                </div>
                <a
                  href={github_url}
                  target="_blank"
                  rel="noopener noreferrer"
                  className="goto-github-btn"
                >
                  <i className="fab fa-github"></i>
                  Github
                </a>
              </article>
            );
          })}
        </div>
      </div>
    </>
  );
};

export default Project;
