import React from "react";
import "./styles/Header.css";
const Header = () => {
  const CloseHumburger = (e) => {
    document.getElementsByClassName("menu-btn-check")[0].checked = false;
  };
  return (
    <>
      <div className="header-img">
        <h1 className="site-title">I'm s3pt3mb3r</h1>
        <a href="#aboutme-title" className="navi">
          About Me
        </a>
        <a href="#contacts-title" className="navi">
          Contacts
        </a>
        <a href="#projects-title" className="navi">
          My Works
        </a>
      </div>
      <div className="hamburger-menu">
        <input type="checkbox" id="menu-btn-check" className="menu-btn-check" />
        <label htmlFor="menu-btn-check" className="menu-btn">
          <span></span>
        </label>
        <div className="menu-content">
          <ul>
            <li>
              <a href="#aboutme-title" onClick={CloseHumburger}>
                About Me
              </a>
            </li>
            <li>
              <a href="#contacts-title" onClick={CloseHumburger}>
                Contacts
              </a>
            </li>
            <li>
              <a href="#projects-title" onClick={CloseHumburger}>
                My Works
              </a>
            </li>
          </ul>
        </div>
      </div>
    </>
  );
};

export default Header;
