import React from "react";
import Preference from "./Preference";
import "./styles/AboutMe.css";

const AboutMe = () => {
  return (
    <>
      <article className="AboutMe-container">
        <h1 id="aboutme-title">
          <i className="uil uil-user-circle"></i>About Me
        </h1>
        <div className="brief-profile">
          <div className="icon-name-wrapper">
            <div className="profile-icon"></div>
            <div className="name-mail">
              <h1 className="profile-name">s3pt3mb3r</h1>
              <h5>s3pt3mb3r@protonmail.com</h5>
            </div>
          </div>
          <div>
            <p>A hobbyist programmer. </p>
            <p>
              {" "}
              <span style={{ color: "#32a875" }}>Likes </span>
              video gaming and painting too.{" "}
            </p>
            <p>
              <span style={{ color: "#b3273c" }}>Hates</span> exams XD
            </p>
          </div>
        </div>

        <div className="preference-wrapper">
          <Preference></Preference>
        </div>
      </article>
    </>
  );
};

export default AboutMe;
