import React from "react";
import "./styles/Sns.css";
import { snsData } from "./sns-data.js";
import { snsIcons } from "./sns-data.js";
const Sns = () => {
  return (
    <>
      <section className="sns-container">
        <h1 id="contacts-title">
          <i className="uil uil-megaphone"></i>Contacts
        </h1>
        <div>
          {snsData.map((data) => {
            const { key, name, color, id, url } = data;
            return (
              <div
                key={key}
                className="social-icon-bg"
                style={{ background: color }}
              >
                <div
                  style={{
                    display: "flex",
                    marginBottom: "2.8rem",
                    alignItems: "center",
                  }}
                >
                  <div
                    className="social-icon"
                    style={{
                      backgroundImage: `url(${snsIcons[key - 1].path})`,
                    }}
                  ></div>
                  <div className="name-id">
                    <h3>{name}</h3>
                    <h5>{id}</h5>
                  </div>
                </div>

                <a
                  href={url}
                  className="sns-btn"
                  target="_blank"
                  rel="noopener noreferrer"
                  style={{
                    border: `solid .5rem ${color}`,
                  }}
                  onMouseEnter={(e) => {
                    e.target.style.background = color;
                  }}
                  onMouseLeave={(e) => {
                    e.target.style.background = "white";
                  }}
                ></a>
              </div>
            );
          })}
        </div>
      </section>
    </>
  );
};

export default Sns;
